<template>
  <div class="videos">
    <div class="block">
      <el-carousel height="650px">
        <el-carousel-item :style="bgStyle[0]">
          <el-row :gutter="10">
            <el-col :span="12">
              <img v-lazy="imageList[0].left" alt="">
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="20" :offset="2">
                  <a href="https://player.vimeo.com/video/192601903" target="_blank">
                    <img v-lazy="imageList[0].play" alt="">
                  </a>
                </el-col>
                <el-col :span="20" :offset="2">
                  <img v-lazy="imageList[0].text" alt="">
                </el-col>
              </el-row>
            </el-col>
          </el-row>
        </el-carousel-item>

        <el-carousel-item :style="bgStyle[1]">
          <el-row>
            <el-col :span="14">
              <el-row>
                <el-col :span="18" :offset="3">
                  <a href="https://player.vimeo.com/video/192464930" target="_blank">
                    <img v-lazy="imageList[1].play" alt="">
                  </a>
                </el-col>
              </el-row>
              <el-row :span="14">
                <el-col :span="24">
                  <img v-lazy="imageList[1].left" alt="">
                </el-col>
              </el-row>
            </el-col>
            <el-col :span="10">
              <img v-lazy="imageList[1].text" alt="" class="trace-text">
            </el-col>
          </el-row>
        </el-carousel-item>

        <el-carousel-item :style="bgStyle[2]">
          <el-row :gutter="10">
            <el-col :span="12">
              <img v-lazy="imageList[2].left" alt="">
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="20" :offset="2">
                  <a href="https://player.vimeo.com/video/194624158" target="_blank">
                    <img v-lazy="imageList[2].play" alt="">
                  </a>
                </el-col>
                <el-col :span="20" :offset="2">
                  <img v-lazy="imageList[2].text" alt="">
                </el-col>
              </el-row>
            </el-col>
          </el-row>
        </el-carousel-item>
      </el-carousel>
    </div>
  </div>
</template>
<script>
import imgUrl from "../../assets/js/imgUrl";
export default {
  data() {
    return {
      imageList: imgUrl.videosList,
      bgStyle: imgUrl.videoStyle
    };
  },
};
</script>
<style lang="scss" scoped>
.videos {
  min-height: 650px;
}

.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0;
}

img {
  width: 100%;
}
.trace-text {
  margin-top: 80px;
}
</style>





