<template>
  <header class="head">
    <el-row>
      <el-col :span="4" :push="1">
        <div class="avatar">
          <img src="./avatar.png" alt="头像">
        </div>
      </el-col>
      <el-col :span="20">
        <el-menu :default-active="activeIndex" class="el-menu-demo" router mode="horizontal" background-color="#24191b" text-color="#494c4d" active-text-color="#73a9af">
          <el-menu-item index="home">Lucas Peng</el-menu-item>
          <el-menu-item index="introduction">Introduction</el-menu-item>
          <el-menu-item index="videos">Videos</el-menu-item>
          <el-menu-item index="images">Images</el-menu-item>
        </el-menu>
      </el-col>
    </el-row>

  </header>
</template>
<script>
export default {
  data() {
    return {
      activeIndex: "home"
    };
  },
  created() {},
  methods: {}
};
</script>

<style lang="scss" scoped>
.el-menu--horizontal {
  border-bottom: none;
}
header {
  background: #24191b;
  .avatar {
    height: 60px;
    img {
      height: 5rem;
      border-radius: 50%;
      // height: 100%;
      border: 0.2rem solid #cccccc;
    }
  }
}

.el-menu-item {
  font-size: 2rem;
}
</style>



